import { decorate } from 'mobx'
import BaseStore from './base-store'
import { API_URL } from '../api'
import { action } from 'mobx'

class HousingComplexStore extends BaseStore {
  constructor() {
    super('housing-complex')
  }

  getUploadPhotoUrl(itemId) {
    const url = `${API_URL}/${this._apiUrl}/${itemId}/photos`
    return url
  }

  onUploadPhoto(item, photo) {
    const index = this.data.indexOf(item)
    this.data[index].photos.push(photo)
  }
}

const HousingComplexDecorated = decorate(HousingComplexStore, {
  getUploadPhotoUrl: action.bound,
  onUploadPhoto: action.bound,
})

export const housingComplexStore = new HousingComplexDecorated()
