import React, { useState, useEffect } from 'react'
import { Input, Button } from 'antd'
import { observer, inject } from 'mobx-react'

import './user-invite.css'

function UserInviteForm(props) {
  const { userStore } = props
  const [email, setEmail] = useState('')
  const [isSubmiting, setIsSubmiting] = useState(false)

  const handleSubmit = async () => {
    setIsSubmiting(true)
    try {
      await userStore.invite(email)
    } catch (e) {}
    setIsSubmiting(false)
    setEmail('')
  }

  return (
    <div className="user-invite">
      <div className="user-invite-input">
        <Input type="email" onChange={e => setEmail(e.target.value)} />
      </div>
      <Button type="primary" onClick={handleSubmit} disabled={isSubmiting}>
         
      </Button>
    </div>
  )
}

export default inject('userStore')(observer(UserInviteForm))
