import React from 'react'
import { Link } from 'react-router-dom'
import { Menu, Button, Row, Col } from 'antd'
import { observer, inject } from 'mobx-react'
import { useLocation } from 'react-router'
import logo from '../../logo.png'

import './nav-bar.css'

function NavBar(props) {
  const { currentUserStore } = props
  const { pathname } = useLocation()

  return (
    <div className="nav-bar">
      <div>
        <Row type="flex" align="center" justify="space-between">
          <Col>
            <Row type="flex" align="center">
              <div
                style={{
                  width: 80,
                  marginRight: 30,
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <Link to="/realty">
                  <img
                    src={logo}
                    style={{
                      width: '100%',
                      height: 'auto',
                      position: 'relative',
                    }}
                  />
                </Link>
              </div>
              <Menu selectedKeys={[pathname]} mode="horizontal" theme="dark">
                <Menu.Item key="/realty">
                  <Link to="/realty"></Link>
                </Menu.Item>
                <Menu.Item key="/buildings">
                  <Link to="/buildings"> / </Link>
                </Menu.Item>
                <Menu.Item key="/housing-complex">
                  <Link to="/housing-complex"> </Link>
                </Menu.Item>
                <Menu.Item key="/developers">
                  <Link to="/developers"></Link>
                </Menu.Item>
                {currentUserStore.isAdmin && (
                  <Menu.Item key="/users">
                    <Link to="/users"></Link>
                  </Menu.Item>
                )}
                <Menu.Item key="/search">
                  <Link to="/search"></Link>
                </Menu.Item>
              </Menu>
            </Row>
          </Col>
          <Col>
            <div className="nav-logout">
              <div className="nav-user-name">
                {currentUserStore.user.fullName}
              </div>
              <Button
                onClick={() => currentUserStore.logOut()}
                title=""
                size="small"
              >
                
              </Button>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  )
}

export default inject('currentUserStore')(observer(NavBar))
