import React, { useEffect } from 'react'
import BuildingsTable from './components/buildings-table'
import BuildingsForm from './components/buildings-form'
import { observer, inject } from 'mobx-react'
import { Tabs, Alert } from 'antd'

const { TabPane } = Tabs

function BuildingsPage(props) {
  const { buildingsStore, currentUserStore } = props

  useEffect(() => {
    if (!buildingsStore.isFetched) {
      buildingsStore.fetch()
    }
  }, [])

  return (
    <>
      {buildingsStore.error && (
        <Alert message={buildingsStore.error.message} type="error" />
      )}
      {currentUserStore.isAdmin ? (
        <Tabs type="card">
          <TabPane tab="/" key="1">
            <BuildingsTable
              data={buildingsStore.data}
              isLoading={buildingsStore.isLoading}
              onUpdate={buildingsStore.fetch.bind(buildingsStore)}
            />
          </TabPane>
          <TabPane tab=" /" key="2">
            <BuildingsForm
              onSubmit={buildingsStore.addItem.bind(buildingsStore)}
            />
          </TabPane>
        </Tabs>
      ) : (
        <BuildingsTable
          isViewOnly
          data={buildingsStore.data}
          isLoading={buildingsStore.isLoading}
          onUpdate={buildingsStore.fetch.bind(buildingsStore)}
        />
      )}
    </>
  )
}

export default inject(
  'buildingsStore',
  'currentUserStore'
)(observer(BuildingsPage))
