import { observable, action, decorate, computed } from 'mobx'
import { client } from '../api'
import { notification } from 'antd'
import qs from 'query-string'

class BaseStore {
  _apiUrl = ''

  data = null

  editableItemId = null

  isLoading = false

  isFetched = false

  error = false

  static messages = {
    addSuccess: '  ',
    addFailure: '  ',
    updateSuccess: '  ',
    updateFailure: '  ',
    removeSuccess: '  ',
    removeFailure: '  ',
  }

  constructor(apiUrl, initialData = null, messages = BaseStore.messages) {
    this._apiUrl = apiUrl
    this.data = initialData
    this.messages = messages
  }

  get isEditingItem() {
    return this.editableItemId !== null
  }

  async getItem(id) {
    let item = null

    if (this.data !== null) {
      item = this.data.find(el => String(el.id) === String(id))
    }

    if (!item) {
      await this.fetch(id)
      if (!this.error) {
        item = this.data.find(el => String(el.id) === String(id))
      }
    }

    return item
  }

  fetch(url, params = {}) {
    this._fetching()

    const strParams = qs.stringify(params)

    const _url =
      url === undefined
        ? this._apiUrl
        : `${this._apiUrl}/${url}${strParams && '?'}${strParams}`

    return client
      .get(_url)
      .then(res => {
        if (Array.isArray(res.data)) {
          this.data = res.data
        } else {
          if (this.data === null) this.data = []
          this.data.push(res.data)
        }
        this._fetchSuccess()
        this.isFetched = true
        return res
      })
      .catch(this._fetchFailure)
  }

  addItem(item) {
    this._fetching()

    return client
      .post(this._apiUrl, item)
      .then(response => {
        if (response.data !== null) {
          this.data = [...(this.data || []), response.data]
          // console.log('- - - this.data', this.data)
          this._fetchSuccess()
          notification.success({ message: this.messages.addSuccess })
        } else {
          notification.error({ message: this.messages.addFailure })
        }
        return response
      })
      .catch(this._fetchFailure)
  }

  updateItem(id, data) {
    this._fetching()

    return client
      .patch(`${this._apiUrl}/${id || this.editableItemId}`, data)
      .then(res => {
        const index = this.data.indexOf(i => i.id === res.data.id)
        this.data[index] = res.data

        if (this.editableItemId) {
          this.editableItemId = null
        }

        notification.success({ message: this.messages.updateSuccess })
        this._fetchSuccess()
      })
      .catch(error => {
        this._fetchFailure(error)
        notification.error({ message: this.messages.updateFailure })
      })
  }

  removeItem(id, url) {
    this._fetching()

    return client
      .delete(`${this._apiUrl}/${id}${url ? '/' + url : ''}`)
      .then(res => {
        this.data = this.data.filter(i => i.id !== id)
        this._fetchSuccess()
        notification.success({ message: this.messages.removeSuccess })
      })
      .catch(error => {
        this._fetchFailure(error)
        notification.error({ message: this.messages.removeFailure })
      })
  }

  editItem(id) {
    this.editableItemId = id
  }

  resetEditItem() {
    this.editableItemId = null
  }

  _fetching() {
    this.isLoading = true
    this.error = null
  }

  _fetchSuccess() {
    this.isLoading = false
  }

  _fetchFailure(error) {
    this.error = error
    this.isLoading = false
  }
}

const BaseStoreDecorated = decorate(BaseStore, {
  data: observable,
  editableItemId: observable,
  isLoading: observable,
  isFetched: observable,
  error: observable,

  isEditingItem: computed,
  editItem: action,
  saveEditItem: action,
  resetEditItem: action,

  getItem: action,
  fetch: action,
  addItem: action,
  updateItem: action,
  removeItem: action,

  _fetching: action.bound,
  _fetchSuccess: action.bound,
  _fetchFailure: action.bound,
})

export default BaseStoreDecorated
