import React, { useEffect, useState } from 'react'
import RealtyTable from './components/realty-table'
import RealtyForm from './components/realty-form'
import { observer, inject } from 'mobx-react'
import { Tabs, Alert } from 'antd'

const { TabPane } = Tabs

function RealtyPage(props) {
  const { realtyStore, currentUserStore } = props

  const [activeTab, setActiveTab] = useState('1')

  useEffect(() => {
    if (!realtyStore.isFetched) {
      realtyStore.fetch()
    }
  }, [])

  useEffect(() => {
    const activeTab = realtyStore.isEditingItem ? '3' : '1'
    setActiveTab(activeTab)
  }, [realtyStore.isEditingItem])

  return (
    <>
      {realtyStore.error && (
        <Alert message={realtyStore.error.message} type="error" />
      )}
      {currentUserStore.isAdmin ? (
        <Tabs type="card" onChange={setActiveTab} activeKey={activeTab}>
          <TabPane tab="" key="1" disabled={realtyStore.isEditingItem}>
            <RealtyTable
              data={realtyStore.data}
              isLoading={realtyStore.isLoading}
              onUpdate={realtyStore.fetch.bind(realtyStore)}
            />
          </TabPane>
          <TabPane
            tab=" "
            key="2"
            disabled={realtyStore.isEditingItem}
          >
            <RealtyForm onSubmit={realtyStore.addItem.bind(realtyStore)} />
          </TabPane>
          {realtyStore.isEditingItem && (
            <TabPane tab=" " key="3">
              <RealtyForm
                onSubmit={realtyStore.updateItem.bind(realtyStore)}
                onCloseEditItem={realtyStore.resetEditItem.bind(realtyStore)}
                editableItem={realtyStore.editableItem}
                isEditable
              />
            </TabPane>
          )}
        </Tabs>
      ) : (
        <RealtyTable
          isViewOnly
          data={realtyStore.data}
          isLoading={realtyStore.isLoading}
          onUpdate={realtyStore.fetch.bind(realtyStore)}
        />
      )}
    </>
  )
}

export default inject('realtyStore', 'currentUserStore')(observer(RealtyPage))
