import React from 'react'
import { Table, Button } from 'antd'
import { Link } from 'react-router-dom'

const columns = [
  {
    title: '',
    render: item => {
      return (
        <Link
          key={item.id}
          to={`housing-complex?id=${item.id}&from=main-table`}
        >
          
        </Link>
      )
    },
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'adress',
    key: 'adress',
  },
]

export default function HousingComplex(props) {
  const { data, isLoading, onUpdate } = props

  return (
    <>
      <nav style={{ marginBottom: 15 }}>
        <Button type="primary" onClick={onUpdate} disabled={isLoading}>
          
        </Button>
      </nav>
      <Table
        dataSource={data}
        columns={columns}
        size="small"
        rowKey="id"
        loading={isLoading}
      />
    </>
  )
}
