import React, { useEffect } from 'react'
import DeveloperForm from './components/developer-form'
import DeveloperTable from './components/developer-table'
import { observer, inject } from 'mobx-react'
import { Tabs, Alert } from 'antd'

const { TabPane } = Tabs

function DevelopersPage(props) {
  const { developersStore, currentUserStore } = props

  useEffect(() => {
    if (!developersStore.isFetched) {
      developersStore.fetch()
    }
  }, [])

  return (
    <>
      {developersStore.error && (
        <Alert message={developersStore.error.message} type="error" />
      )}
      {currentUserStore.isAdmin ? (
        <Tabs type="card">
          <TabPane tab="" key="1">
            <DeveloperTable
              data={developersStore.data}
              isLoading={developersStore.isLoading}
              onUpdate={developersStore.fetch.bind(developersStore)}
            />
          </TabPane>
          <TabPane tab=" " key="2">
            <DeveloperForm
              error={developersStore.error}
              onSubmit={developersStore.addItem.bind(developersStore)}
            />
          </TabPane>
        </Tabs>
      ) : (
        <DeveloperTable
          isViewOnly
          data={developersStore.data}
          isLoading={developersStore.isLoading}
          onUpdate={developersStore.fetch.bind(developersStore)}
        />
      )}
    </>
  )
}

export default inject(
  'developersStore',
  'currentUserStore'
)(observer(DevelopersPage))
