import { decorate, action } from 'mobx'
import BaseStore from './base-store'
import { client } from '../api'

/**
 * 
 */
class UserStore extends BaseStore {
  constructor() {
    super('user')
  }

  invite(email) {
    return client.post(`${this._apiUrl}/invite`, { email })
  }
}

const UserStoreDecorated = decorate(UserStore, {
  invite: action,
})

export const userStore = new UserStoreDecorated()
