import axios from 'axios'

// export const BASE_URL = 'http://10.0.0.101:8088'
// export const BASE_URL = 'http://localhost:8088'
export const BASE_URL = 'http://37.230.116.140:8088'
// export const BASE_URL = 'http://37.230.116.140:8090'
export const API_URL = BASE_URL + '/api/v1'

/**
  
  /api/{entity}/add
   entity   user, realty, building, developer, district
   POST ,   body  
  /api/{entity}/update
  /api/{entity}/delete
 
*/

const axiosInstance = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  // baseURL: 'http://localhost:8088/api/v1',
  // headers: { 'X-Custom-Header': 'foobar' },
})

export const client = {
  get: (url, params) => axiosInstance(url, params),
  post: (url, params) => axiosInstance.post(url, params),
  delete: url => axiosInstance.delete(url),
  patch: (url, params) => axiosInstance.patch(url, params),
}
