import React, { useState, useEffect, useCallback } from 'react'
import { Tabs } from 'antd'
import { observer, inject } from 'mobx-react'
import HousingComplex from './housing-complex'
import HousingComplexForm from './housing-complex-form'
import HousingComplexTable from './housing-complex-table'
import qs from 'query-string'
import _ from 'lodash'

const { TabPane } = Tabs

function HousingComplexContainer(props) {
  const {
    buildingsStore,
    realtyStore,
    housingComplexStore,
    location: { search },
    realtyTypeStore,
    districtStore,
    developersStore,
  } = props
  const { id, from } = qs.parse(search)

  let store = housingComplexStore
  if (from === 'realty') store = realtyStore
  if (from === 'buildings') store = buildingsStore

  const [data, setData] = useState(null)
  const [isFetching, setIsFetching] = useState(false)
  const [isEditing, setIsEditing] = useState(false)
  const [activeTab, setActiveTab] = useState(
    id === undefined ? 'table' : 'card'
  )
  const [isSubmiting, setIsSubmiting] = useState(false)

  const handleEdit = useCallback(() => {
    setIsEditing(true)
    setActiveTab('form')
  }, [])
  const handleChangeTab = useCallback(key => setActiveTab(key), [])

  const handleUpdate = useCallback(
    async data => {
      setIsSubmiting(true)
      await store.updateItem(id, data)
      setIsEditing(false)
      setActiveTab('card')
      setIsSubmiting(false)
    },
    [store, id]
  )
  const handleCreate = useCallback(
    async data => {
      setIsSubmiting(true)
      const response = await store.addItem(data)
      setIsSubmiting(false)
      return response
    },
    [store]
  )

  useEffect(() => {
    async function getData() {
      setIsFetching(true)

      if (id) {
        const data = await store.getItem(id)
        if (from === 'buildings') setData(data.housingComplex)
        if (from === 'realty') setData(data.building.housingComplex)
        if (from === 'main-table') setData(data)

        console.log('- - - data', data)
        setActiveTab('card')
      } else {
        setActiveTab('table')
        setData(null)
      }

      if (!housingComplexStore.isFetched) {
        housingComplexStore.fetch()
      }

      setIsFetching(false)
    }

    getData()
  }, [store, id, housingComplexStore, from])

  useEffect(() => {
    if (!realtyTypeStore.isFetched) {
      realtyTypeStore.fetch()
    }
    if (!districtStore.isFetched) {
      districtStore.fetch()
    }
    if (!developersStore.isFetched) {
      developersStore.fetch()
    }
  }, [developersStore, districtStore, realtyTypeStore])

  if (isFetching) {
    return <>...</>
  }

  const title = isEditing ? '  ' : '  '

  const EditForm = (
    <HousingComplexForm
      isSubmiting={isSubmiting}
      isEditable={isEditing}
      onSubmit={isEditing ? handleUpdate : handleCreate}
      initialValue={null}
      title={title}
      realtyTypes={realtyTypeStore.data}
      developers={developersStore.data}
      districts={districtStore.data}
      uploadPhotoUrl={data && housingComplexStore.getUploadPhotoUrl(data.id)}
    />
  )

  return (
    <Tabs
      type="card"
      activeKey={activeTab}
      onChange={handleChangeTab}
      onTabScroll="bottom"
    >
      <TabPane
        tab="  "
        key="table"
        disabled={housingComplexStore.isEditingItem}
      >
        <HousingComplexTable
          data={housingComplexStore.data}
          isLoading={housingComplexStore.isLoading}
          onUpdate={housingComplexStore.fetch.bind(housingComplexStore)}
        />
      </TabPane>
      {data && (
        <TabPane tab=" " key="card" disabled={isEditing}>
          <HousingComplex
            data={data}
            onEdit={handleEdit}
            onRemove={housingComplexStore.removeItem.bind(housingComplexStore)}
            uploadPhotoUrl={
              data ? housingComplexStore.getUploadPhotoUrl(data.id) : null
            }
            onUploadPhoto={housingComplexStore.onUploadPhoto.bind(
              housingComplexStore
            )}
          />
        </TabPane>
      )}
      <TabPane tab={title} key="form">
        {EditForm}
      </TabPane>
    </Tabs>
  )
}

export default inject(
  'buildingsStore',
  'realtyStore',
  'housingComplexStore',
  'realtyTypeStore',
  'districtStore',
  'developersStore'
)(observer(HousingComplexContainer))
