import React from 'react'
import {
  DatePicker,
  Form,
  Input,
  TimePicker,
  Select,
  Switch,
  InputNumber,
  Checkbox,
} from 'antd'
// import { InputAutocomplete } from '../../input'
import ruLocale from 'antd/lib/date-picker/locale/ru_RU'

import 'moment/locale/ru'

const FormItem = Form.Item
const { Option } = Select

const CreateAntField = AntComponent => ({
  field,
  form,
  hasFeedback,
  label,
  selectOptions,
  submitCount,
  type,
  formItemLayout = {},
  onChange,
  className,
  required,
  ...props
}) => {
  const touched = form.touched[field.name]
  const submitted = submitCount > 0
  const hasError = form.errors[field.name]
  const submittedError = hasError && submitted
  const touchedError = hasError && touched

  const handleInputChange = ({ target: { value } }) =>
    form.setFieldValue(field.name, value)

  const handleChange = value => {
    form.setFieldValue(field.name, value)
    if (onChange) onChange(value)
  }
  const handleBlur = () => form.setFieldTouched(field.name, true)

  return (
    <div className={className}>
      <FormItem
        label={label}
        required={required}
        hasFeedback={
          (hasFeedback && submitted) || (hasFeedback && touched) ? true : false
        }
        help={submittedError || touchedError ? hasError : false}
        validateStatus={submittedError || touchedError ? 'error' : ''}
        {...formItemLayout}
      >
        <AntComponent
          {...field}
          {...props}
          onBlur={handleBlur}
          onChange={type ? handleInputChange : handleChange}
          locale={ruLocale}
        >
          {selectOptions &&
            selectOptions.map(({ text, value, id }) => (
              <Option key={id} value={value}>
                {text}
              </Option>
            ))}
        </AntComponent>
      </FormItem>
    </div>
  )
}

export const AntTimePicker = CreateAntField(TimePicker)
export const AntDatePicker = CreateAntField(DatePicker)
export const AntInput = CreateAntField(Input)
// export const AntInputAutocomplete = CreateAntField(InputAutocomplete)
export const AntSelect = CreateAntField(Select)
export const AntSwitch = CreateAntField(Switch)
export const AntInputNumber = CreateAntField(InputNumber)
export const AntChecbox = CreateAntField(Checkbox)
