import { observable, computed, action, decorate } from 'mobx'

import { client } from '../api'
class SearchStore {
  _initialValues = {
    priceFrom: null,
    priceTo: null,
    areaFrom: null,
    areaTo: null,
    amountBedrooms: null,
    deadline: null,
    districtId: null,
    housingComplexId: null,
    floorFrom: null,
    floorTo: null,
    status: null,
  }

  isLoading = false

  fields = {
    ...this._initialValues,
  }

  result = []

  error = null

  setField(key, value) {
    this.fields = {
      ...this.fields,
      [key]: value,
    }
  }

  search() {
    this.isLoading = true

    client
      .post('search', this.fields)
      .then(response => {
        this.result = response.data
        this.isLoading = false
      })
      .catch(error => {
        this.error = error
        this.isLoading = false
      })
  }

  reset() {
    this.error = null
    this.result = []
    this.fields = {
      ...this._initialValues,
    }
  }
}

const SearchStoreDecorated = decorate(SearchStore, {
  result: observable,
  isLoading: observable,
  fields: observable,
  error: observable,
  setField: action,
  search: action,
  reset: action,
})

export const searchStore = new SearchStoreDecorated()
