import React, { useState } from 'react'
import { Formik, Form, Field } from 'formik'
import { Button, Row, Col } from 'antd'
import {
  AntInput,
  AntDatePicker,
  AntChecbox,
  AntInputNumber,
} from '../../../../components/form/create-antd-field'
import moment from 'moment'

export default function RealtyForm(props) {
  const [isSubmitting, setIsSubmitting] = useState(false)
  const { onSubmit, isEditable, editableItem, onCloseEditItem } = props

  const initialValues = isEditable
    ? {
        description: editableItem?.description || '',
        address: editableItem?.address || '',
        amountBedrooms: editableItem?.amountBedrooms,
        area: editableItem?.area,
        buildingId: editableItem?.buildingId,
        ceilingHeight: editableItem?.ceilingHeight,
        deadline: moment(editableItem?.deadline),
        floor: editableItem?.floor,
        info: editableItem?.info,
        price: editableItem?.price,
        realtyType: editableItem?.realtyType,
        rented: editableItem?.rented,
        secondary: editableItem?.secondary,
      }
    : {
        description: '',
        address: '',
        amountBedrooms: '',
        area: null,
        buildingId: 1,
        ceilingHeight: null,
        deadline: null,
        floor: null,
        info: '',
        price: null,
        realtyType: '',
        rented: false,
        secondary: false,
      }

  return (
    <Formik
      onSubmit={async (values, form) => {
        if (values.rented instanceof Object) {
          values.rented = values.rented.target.checked
        }
        if (values.secondary instanceof Object) {
          values.secondary = values.secondary.target.checked
        }

        setIsSubmitting(true)

        if (isEditable) {
          await onSubmit(null, values)
        } else {
          await onSubmit(values)
          form.resetForm()
        }

        setIsSubmitting(false)
      }}
      initialValues={initialValues}
    >
      {() => {
        return (
          <Form className="form">
            <Row type="flex" gutter={20}>
              <Col span={8}>
                <Field
                  type="text"
                  label=""
                  name="description"
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="address"
                  label=""
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  name="amountBedrooms"
                  label=" "
                  component={AntInputNumber}
                />
              </Col>
              <Col span={8}>
                <Field name="area" label="" component={AntInputNumber} />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="buildingId"
                  label="  "
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  name="ceilingHeight"
                  label=" "
                  component={AntInputNumber}
                />
              </Col>
              <Col span={8}>
                <Field
                  name="deadline"
                  label=" "
                  component={AntDatePicker}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="floor"
                  label=""
                  component={AntInput}
                />
              </Col>
              {/* <Col span={8}>
              <Field
                type="text"
                name="id"
                label="  "
                component={AntInput}
              />
            </Col> */}
              <Col span={8}>
                <Field
                  type="text"
                  name="info"
                  label="  "
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="price"
                  label=""
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="pricePerSquareMeter"
                  component={AntInput}
                  label="   "
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="realtyType"
                  label=" "
                  component={AntInput}
                />
              </Col>
              <Col span={4}>
                <Field name="rented" label="" component={AntChecbox} />
              </Col>
              <Col span={4}>
                <Field
                  name="secondary"
                  label=""
                  component={AntChecbox}
                />
              </Col>
              {/* <Col span={8}>
              <Field type="text" name="" label="" component={AntInput} />
            </Col>
            <Col span={8}>
              <Field type="text" name="" label="" component={AntInput} />
            </Col> */}
            </Row>
            <Row type="flex" justify="center" gutter={20}>
              <Col span={8}>
                {isEditable ? (
                  <Row type="flex" justify="space-between">
                    <Button
                      type="primary"
                      htmlType="submit"
                      style={{ width: '48%', marginTop: 20 }}
                      disabled={isSubmitting}
                    >
                      
                    </Button>
                    <Button
                      type="danger"
                      style={{ width: '48%', marginTop: 20 }}
                      disabled={isSubmitting}
                      onClick={onCloseEditItem}
                    >
                      
                    </Button>
                  </Row>
                ) : (
                  <Button
                    type="primary"
                    htmlType="submit"
                    style={{ width: '100%', marginTop: 20 }}
                    disabled={isSubmitting}
                  >
                    
                  </Button>
                )}
              </Col>
            </Row>
          </Form>
        )
      }}
    </Formik>
  )
}
