import React from 'react'
import { Table, Button, Select } from 'antd'
import { userStore, currentUserStore } from '../../../../store'
import { USER_ROLES } from '../../../../permission'

const { Option } = Select

const columns = [
  {
    title: 'id',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: 'id ',
    dataIndex: 'roleId',
    key: 'roleId',
    render: (roleId, item) => {
      if (!currentUserStore.isAdmin) {
        return item.roleId
      }

      return (
        <Select
          defaultValue={roleId}
          onChange={async roleId => {
            await userStore.updateItem(item.id, { roleId })
          }}
        >
          {Object.values(USER_ROLES).map(role => (
            <Option value={role.id} key={role.id}>
              {role.label}
            </Option>
          ))}
        </Select>
      )
    },
  },
  {
    title: '',
    dataIndex: 'company',
    key: 'company',
  },
  {
    title: '',
    dataIndex: 'phoneNumber',
    key: 'phoneNumber',
  },
  {
    title: 'email',
    dataIndex: 'email',
    key: 'email',
  },
  {
    title: '',
    dataIndex: 'city',
    key: 'city',
  },
]

function UserTable(props) {
  const { data, isLoading, onUpdate } = props

  return (
    <>
      <nav style={{ marginBottom: 15 }}>
        <Button type="primary" onClick={onUpdate} disabled={isLoading}>
          
        </Button>
      </nav>
      <Table
        dataSource={data}
        columns={columns}
        size="small"
        rowKey="id"
        loading={isLoading}
      />
    </>
  )
}

export default UserTable
