import React, { useState } from 'react'
import { Formik, Form, Field } from 'formik'
import { Button, Row, Col } from 'antd'
import { AntInput } from '../../../../components/form/create-antd-field'

export default function RealtyForm(props) {
  const [isSubmitting, setIsSubmitting] = useState(false)
  const { onSubmit } = props

  return (
    <>
      <Formik
        initialValues={{
          name: '',
          info: '',
          commonContacts: '',
          viewContacts: '',
          viewTime: '',
        }}
        onSubmit={async (values, form) => {
          setIsSubmitting(true)

          await onSubmit(values)

          form.resetForm()
          setIsSubmitting(false)
        }}
      >
        {() => (
          <Form className="form">
            <Row type="flex" gutter={20}>
              <Col span={8}>
                <Field
                  type="text"
                  name="name"
                  label=""
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="info"
                  label="  "
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="commonContacts"
                  label="  ()"
                  component={AntInput}
                />
              </Col>
              <Col span={8}>
                <Field
                  type="text"
                  name="viewContacts"
                  label="   "
                  component={AntInput}
                />
              </Col>
            </Row>
            <Row type="flex" justify="center">
              <Col span={8}>
                <Button
                  type="primary"
                  htmlType="submit"
                  style={{ width: '100%', marginTop: 20 }}
                  disabled={isSubmitting}
                >
                  
                </Button>
              </Col>
            </Row>
          </Form>
        )}
      </Formik>
    </>
  )
}
