import React from 'react'
import { inject, observer } from 'mobx-react'
import { Form, Icon, Input, Button, Checkbox, Row, Col, Alert } from 'antd'

import './login.css'

function LoginForm(props) {
  const { currentUserStore, form } = props
  const { getFieldDecorator } = form

  const handleSubmit = e => {
    e.preventDefault()
    form.validateFields(async (err, values) => {
      if (!err) {
        await currentUserStore.signIn(values)
        form.resetFields()
      }
    })
  }

  const message = ' !'

  return (
    <>
      {currentUserStore.error && (
        <Alert type="error" message={currentUserStore.error.message} />
      )}
      <Form onSubmit={handleSubmit} className="login-form">
        <Form.Item>
          <div>E-mail:</div>
          {getFieldDecorator('username', {
            rules: [{ required: true, message }],
          })(
            <Input
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
            />
          )}
        </Form.Item>
        <Form.Item>
          <div>:</div>
          {getFieldDecorator('password', {
            rules: [{ required: true, message }],
          })(
            <Input
              prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
              type="password"
            />
          )}
        </Form.Item>
        <Form.Item>
          <div className="login-form-forgot">
            <Row type="flex" justify="end">
              <Col>
                {/* {getFieldDecorator('remember', {
                valuePropName: 'checked',
                initialValue: true,
              })(<Checkbox></Checkbox>)} */}
                <Checkbox></Checkbox>
              </Col>
              <Col>
                <a href=""> </a>
              </Col>
            </Row>
            <div className="login-form-footer">
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
                loading={currentUserStore.isLoading}
                size="large"
              >
                
              </Button>
            </div>
          </div>
        </Form.Item>
      </Form>
    </>
  )
}

const WrappedLoginForm = Form.create({})(LoginForm)

export default inject('currentUserStore')(observer(WrappedLoginForm))
