import React from 'react'
import { Table, Button } from 'antd'
import { Link } from 'react-router-dom'
import { realtyStore } from '../../../../store'

const columns = [
  {
    title: '',
    render: item => (
      <Link key={item.id} to={`housing-complex?id=${item.id}&from=realty`}>
        
      </Link>
    ),
  },
  {
    title: 'id',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: '',
    dataIndex: 'address',
    key: 'address',
  },
  {
    title: '',
    dataIndex: 'realtyType',
    key: 'realtyType',
  },
  {
    title: '',
    dataIndex: 'floor',
    key: 'floor',
  },
  {
    title: '',
    dataIndex: 'area',
    key: 'area',
  },
  {
    title: '',
    dataIndex: 'price',
    key: 'price',
  },
  {
    title: '  ',
    dataIndex: 'pricePerSquareMeter',
    key: 'pricePerSquareMeter',
  },
  {
    title: ' ',
    dataIndex: 'amountBedrooms',
    key: 'amountBedrooms',
  },
  {
    title: ' ',
    dataIndex: 'ceilingHeight',
    key: 'ceilingHeight',
  },
  {
    title: '',
    dataIndex: 'rented',
    key: 'rented',
    render: value => (value ? '+' : '-'),
  },
  {
    title: '',
    dataIndex: 'secondary',
    key: 'secondary',
    render: value => (value ? '+' : '-'),
  },
  {
    title: ' ',
    dataIndex: 'deadline',
    key: 'deadline',
  },
  {
    title: '  ',
    dataIndex: 'info',
    key: 'info',
  },
  {
    title: '',
    dataIndex: null,
    key: '_nav',
    render: item => (
      <Button.Group>
        <Button
          onClick={() => realtyStore.removeItem(item.id)}
          type="primary"
          icon="delete"
        />
        <Button
          onClick={() => realtyStore.editItem(item.id)}
          type="primary"
          icon="edit"
        />
      </Button.Group>
    ),
  },
]

export default function RealtyTable(props) {
  const { data, isLoading, onUpdate, isViewOnly } = props

  const nextColumns = isViewOnly
    ? columns.filter(column => '_nav' !== column.key)
    : columns

  return (
    <>
      <nav style={{ marginBottom: 15 }}>
        <Button type="primary" onClick={onUpdate} disabled={isLoading}>
          
        </Button>
      </nav>
      <Table
        dataSource={data}
        columns={nextColumns}
        size="small"
        rowKey="id"
        loading={isLoading}
      />
    </>
  )
}
