import React from 'react'
import {
  Input,
  Col,
  Row,
  Select,
  InputNumber,
  DatePicker,
  Button,
  Form,
} from 'antd'

import './search-nav.css'

const InputGroup = Input.Group

export default function SearchNav(props) {
  const {
    fields,
    onChangeField,
    onReset,
    onSearch,
    isSubmiting,
    realtyTypes,
    districts,
  } = props

  return (
    <div className="search-nav">
      <Form>
        <Row type="flex" gutter={20}>
          <Col span={4}>
            <label>  - </label>
            <InputGroup compact>
              <InputNumber
                style={{ width: '50%' }}
                onChange={value => onChangeField('priceFrom', value)}
                value={fields.priceFrom}
              />
              <InputNumber
                style={{ width: '50%' }}
                onChange={value => onChangeField('priceTo', value)}
                value={fields.priceTo}
              />
            </InputGroup>
          </Col>
          <Col span={4}>
            <label>  - </label>
            <InputGroup compact>
              <InputNumber
                style={{ width: '50%' }}
                onChange={value => onChangeField('areaFrom', value)}
                value={fields.areaFrom}
              />
              <InputNumber
                style={{ width: '50%' }}
                onChange={value => onChangeField('areaTo', value)}
                value={fields.areaTo}
              />
            </InputGroup>
          </Col>
          <Col span={4}>
            <label>  - </label>
            <InputGroup compact>
              <InputNumber
                style={{ width: '50%' }}
                onChange={value => onChangeField('floorFrom', value)}
                value={fields.floorFrom}
              />
              <InputNumber
                style={{ width: '50%' }}
                onChange={value => onChangeField('floorTo', value)}
                value={fields.floorTo}
              />
            </InputGroup>
          </Col>
          <Col span={4}>
            <label></label>
            <InputNumber
              style={{ width: '100%' }}
              onChange={value => onChangeField('amountBedrooms', value)}
              value={fields.amountBedrooms}
            />
          </Col>
          <Col span={4}>
            <label> </label>
            <Select
              value={fields.districtId}
              onChange={value => onChangeField('districtId', value)}
            >
              {districts &&
                districts.map(district => (
                  <Select.Option key={district.id} value={district.id}>
                    {district.name}
                  </Select.Option>
                ))}
            </Select>
          </Col>
        </Row>

        <br />

        <Row type="flex" gutter={20}>
          <Col span={4}>
            <label></label>
            <Input
              value={fields.housingComplexId}
              onChange={event =>
                onChangeField('housingComplexId', event.target.value)
              }
            />
          </Col>
          <Col span={4}>
            <label> </label>
            <Select
              value={fields.status}
              onChange={value => onChangeField('status', value)}
            >
              {realtyTypes &&
                realtyTypes.map(item => (
                  <Select.Option value={item.id} key={item.id}>
                    {item.name}
                  </Select.Option>
                ))}
            </Select>
          </Col>
          <Col span={4}>
            <label> </label>
            <DatePicker
              style={{ width: '100%' }}
              onChange={(dateMoment, dateString) =>
                onChangeField('deadline', dateMoment.format())
              }
              value={fields.deadline}
            />
          </Col>
          <Col span={4}>
            <label></label>
            <Input onChange={() => {}} />
          </Col>
          <Col span={4}>
            <Button.Group style={{ width: '100%', marginTop: 24 }}>
              <Button type="danger" style={{ width: '50%' }} onClick={onReset}>
                
              </Button>
              <Button
                type="primary"
                style={{ width: '50%' }}
                onClick={onSearch}
                loading={isSubmiting}
              >
                
              </Button>
            </Button.Group>
          </Col>
        </Row>
      </Form>
    </div>
  )
}
