import React from 'react'
import { Table, Button } from 'antd'
import { developersStore } from '../../../../store'

const columns = [
  {
    title: 'id',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: ' ',
    dataIndex: 'commonContacts',
    key: 'commonContacts',
  },
  {
    title: '  ',
    dataIndex: 'info',
    key: 'info',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '   ',
    dataIndex: 'viewContacts',
    key: 'viewContacts',
  },
  {
    title: ' ',
    dataIndex: 'viewTime',
    key: 'viewTime',
  },
  {
    title: '',
    dataIndex: null,
    key: '_nav',
    render: item => (
      <Button
        onClick={() => developersStore.removeItem(item.id)}
        type="primary"
        icon="delete"
      />
    ),
  },
]

export default function DeveloperTable(props) {
  const { data, isLoading, onUpdate, isViewOnly } = props

  const nextColumns = isViewOnly
    ? columns.filter(column => '_nav' !== column.key)
    : columns

  return (
    <>
      <nav style={{ marginBottom: 15 }}>
        <Button type="primary" onClick={onUpdate} disabled={isLoading}>
          
        </Button>
      </nav>
      <Table
        dataSource={data}
        columns={nextColumns}
        size="small"
        rowKey="id"
        loading={isLoading}
      />
    </>
  )
}
