import React, { useEffect } from 'react'
import { Alert } from 'antd'
import UserTable from './components/user-table'
import UserInviteForm from './components/user-invite-form'
import { inject, observer } from 'mobx-react'

function UsersPage(props) {
  const { userStore } = props

  useEffect(() => {
    if (!userStore.isFetched) {
      userStore.fetch()
    }
  }, [])

  return (
    <>
      {userStore.error && (
        <Alert type="error" message={userStore.error.message} />
      )}
      <UserInviteForm />
      <UserTable data={userStore.data} isLoading={userStore.isLoading} />
    </>
  )
}

export default inject('userStore')(observer(UsersPage))
