import React from 'react'

import { Upload, Modal } from 'antd'
import { PlusOutlined } from '@ant-design/icons'

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => resolve(reader.result)
    reader.onerror = error => reject(error)
  })
}

export default class PicturesWall extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      previewVisible: false,
      previewImage: '',
      previewTitle: '',
      fileList: this.filesToMap(this.transformFiles(props.photos)),
    }
  }

  handleCancel = () => this.setState({ previewVisible: false })

  handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj)
    }

    this.setState({
      previewImage: file.url || file.preview,
      previewVisible: true,
      previewTitle:
        file.name || file.url.substring(file.url.lastIndexOf('/') + 1),
    })
  }

  handleChange = ({ fileList, file }) => {
    if (file.status === 'done') {
      console.log('ph id', file.response.id)
      this.props.onUpload(file.response)
    }
    this.setState({ fileList: this.filesToMap(fileList) })
  }

  handleRemove = file => {
    const fileId = file.response ? file.response.id : file.id
    const uploaded = this.props.photos.find(photo => photo.id === fileId)
    if (uploaded) this.props.onRemove(fileId)
  }

  componentDidUpdate(prevProps, prevState) {
    const { fileList } = this.state
    const { photos } = this.props

    if (prevProps.photos !== this.props.photos) {
      this.setState({
        fileList: {
          ...fileList,
          ...this.filesToMap(this.transformFiles(photos)),
        },
      })
    }
  }

  filesToMap = files => {
    return files.reduce((acc, item) => {
      acc[item.uid] = item
      return acc
    }, {})
  }

  transformFiles = files => {
    return files.map((item, index) =>
      Object.assign(item, { uid: '-' + String(index + 1) })
    )
  }

  render() {
    const { previewVisible, previewImage, previewTitle, fileList } = this.state
    const { uploadUrl } = this.props

    return (
      <div className="clearfix">
        <Upload
          action={uploadUrl}
          listType="picture-card"
          fileList={Object.values(fileList)}
          onPreview={this.handlePreview}
          onChange={this.handleChange}
          onRemove={this.handleRemove}
          multiple
        >
          <div>
            <PlusOutlined />
            <div className="ant-upload-text">Upload</div>
          </div>
        </Upload>
        <Modal
          visible={previewVisible}
          title={previewTitle}
          footer={null}
          onCancel={this.handleCancel}
        >
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </div>
    )
  }
}
