import { observable, computed, action, decorate } from 'mobx'
import { client, BASE_URL } from '../api'
import axios from 'axios'
import { USER_ROLES } from '../permission'
import qs from 'query-string'
import { notification } from 'antd'
import { getUrlError } from '../utils'

class CurrentUserStore {
  user = null
  // {
  //   roleId: USER_ROLES.ADMIN.id,
  // }

  isLoading = false

  error = null

  get isAuth() {
    return this.user !== null
  }

  get isAdmin() {
    return (
      this.user &&
      (currentUserStore.user.roleId === USER_ROLES.ADMIN.id ||
        currentUserStore.user.roleId === USER_ROLES.MANAGER.id)
    )
  }

  signUp(user, cb) {
    this._fetching()

    return client
      .post('user', user)
      .then(() => {
        notification.success({ message: '  ' })
        this.isLoading = false
        if (cb) cb()
      })
      .catch(this._fetchFailure)
  }

  signIn(user) {
    this._fetching()

    return axios({
      method: 'post',
      url: BASE_URL + '/login',
      data: qs.stringify(user),
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    })
      .then(() => {
        client
          .get('user/current-user')
          .then(this._fetchSuccess)
          .catch(this._fetchFailure)
      })
      .catch(this._fetchFailure)
  }

  logOut() {
    axios({
      method: 'post',
      url: BASE_URL + '/logout',
    })
      .then(res => {
        this.user = null
      })
      .catch(this._fetchFailure)
  }

  _fetching() {
    this.isLoading = true
    this.error = null
  }

  _fetchSuccess(res) {
    if (getUrlError()) {
      this._fetchFailure(new Error(' '))
    } else {
      this.user = res.data
      this.isLoading = false
    }
  }

  _fetchFailure(error) {
    this.error = error
    this.isLoading = false
    notification.error({ message: error.message })
  }
}

const CurrentUserStoreDecorated = decorate(CurrentUserStore, {
  user: observable,
  isAuth: computed,
  isAdmin: computed,

  logIn: action,
  signIn: action,
  logOut: action,

  _fetching: action.bound,
  _fetchSuccess: action.bound,
  _fetchFailure: action.bound,
})

export const currentUserStore = new CurrentUserStoreDecorated()
