import React, { useEffect } from 'react'
import SearchNav from './components/search-nav'
import SearchResultTable from './components/search-result-table'
import { observer, inject } from 'mobx-react'
import { Alert } from 'antd'

function Search(props) {
  const { searchStore, realtyTypeStore, districtStore } = props

  useEffect(() => {
    if (!realtyTypeStore.isFetched) {
      realtyTypeStore.fetch()
    }
    if (!districtStore.isFetched) {
      districtStore.fetch()
    }
  }, [districtStore, realtyTypeStore])

  return (
    <>
      <SearchNav
        fields={searchStore.fields}
        realtyTypes={realtyTypeStore.data}
        districts={districtStore.data}
        onChangeField={searchStore.setField.bind(searchStore)}
        onReset={searchStore.reset.bind(searchStore)}
        onSearch={searchStore.search.bind(searchStore)}
        isSubmiting={searchStore.isLoading}
      />
      {searchStore.error && (
        <Alert type="error" message={searchStore.error.message} />
      )}
      <SearchResultTable data={searchStore.result} />
    </>
  )
}

export default inject(
  'searchStore',
  'realtyTypeStore',
  'districtStore'
)(observer(Search))
