import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom'
import { observer, inject } from 'mobx-react'
import SearchPage from '../pages/search'
import RealtyPage from '../pages/realty'
import BuildingsPage from '../pages/buildings'
import HousingComplexPage from '../pages/housing-complex'
import DevelopersPage from '../pages/developers'
import UsersPage from '../pages/users'

function Routes(props) {
  const { currentUserStore } = props

  return (
    <Switch>
      <Route path="/realty" exact component={RealtyPage} />
      <Route path="/buildings" component={BuildingsPage} />
      <Route path="/housing-complex:id?" component={HousingComplexPage} />
      <Route path="/developers" component={DevelopersPage} />
      {currentUserStore.isAdmin && (
        <Route path="/users" component={UsersPage} />
      )}
      <Route path="/search" component={SearchPage} />
      <Redirect to="/realty" />
    </Switch>
  )
}

export default inject('currentUserStore')(observer(Routes))
