import React from 'react'
import { Form, Input, Button, Row, Alert } from 'antd'
import { inject, observer } from 'mobx-react'

import './create-user.css'

function CreateUserForm(props) {
  const { form, currentUserStore, onCreated } = props
  const { getFieldDecorator } = form

  const handleSubmit = e => {
    e.preventDefault()
    form.validateFields(async (err, values) => {
      if (!err) {
        await currentUserStore.signUp(values, onCreated)
        form.resetFields()
      }
    })
  }

  const message = ' !'

  return (
    <>
      {currentUserStore.error && (
        <Alert type="error" message={currentUserStore.error.message} />
      )}
      <div className="create-user">
        <Form onSubmit={handleSubmit}>
          <Form.Item>
            <Form.Item>
              <div>E-mail:</div>
              {getFieldDecorator('email', {
                rules: [{ required: true, message }],
              })(<Input type="email" />)}
            </Form.Item>
            <Form.Item>
              <div>:</div>
              {getFieldDecorator('password', {
                rules: [{ required: true, message }],
              })(<Input type="password" />)}
            </Form.Item>
            <div>:</div>
            {getFieldDecorator('fullName', {
              rules: [{ required: true, message }],
            })(<Input type="text" />)}
          </Form.Item>
          <Form.Item>
            <div>  :</div>
            {getFieldDecorator('phoneNumber', {
              rules: [{ required: true, message }],
            })(<Input type="tel" />)}
          </Form.Item>
          <Form.Item>
            <div>:</div>
            {getFieldDecorator('company', {
              rules: [{ required: true, message }],
            })(<Input type="text" />)}
          </Form.Item>
          <Form.Item>
            <div>:</div>
            {getFieldDecorator('city', {
              rules: [{ required: true, message }],
            })(<Input type="text" />)}
          </Form.Item>
          <Form.Item>
            <div> :</div>
            {getFieldDecorator('secretCode', {
              rules: [{ required: true, message }],
            })(<Input type="text" />)}
          </Form.Item>
          {/* <Form.Item
        {getFieldDecorator('photo', { rules: [{ required: true, message }] })(
          <Input placeholder="" type="file" />
        )}
      </Form.Item> */}
          <Row type="flex" justify="center">
            <Button
              className="create-user-button"
              htmlType="submit"
              type="primary"
              loading={currentUserStore.isLoading}
              size="large"
            >
              
            </Button>
          </Row>
        </Form>
      </div>
    </>
  )
}

const WrappedCreateUserForm = Form.create({})(CreateUserForm)

export default inject('currentUserStore')(observer(WrappedCreateUserForm))
