import React from 'react'
import ReactDOM from 'react-dom'
import App from './app'
import { HashRouter } from 'react-router-dom'
import { Provider } from 'mobx-react'
import * as store from './store'

ReactDOM.render(
  <Provider {...store}>
    <HashRouter>
      <App />
    </HashRouter>
  </Provider>,
  document.getElementById('root')
)
